@import url(https://fonts.googleapis.com/css?family=Lato|Raleway:400,500,600,700&display=swap);
.Navbar{position:fixed;top:0;left:0;right:0;height:110px;background:#64bfc8;-webkit-transition:0.3s all ease-in;transition:0.3s all ease-in;font-family:"Lato", sans-serif;z-index:10}.Navbar.Navbar--collapsed{height:90px;background:rgba(100,191,200,0.9)}.Navbar.Navbar--collapsed .Navbar__Container .Navbar__Logo{-webkit-transform:translateX(-50%) scale(0.6);transform:translateX(-50%) scale(0.6)}.Navbar .Navbar__Container{position:relative;height:100%;padding:0 30px;display:flex;justify-content:space-between;align-items:center}.Navbar .Navbar__Container .Navbar__SubContainer{display:flex;justify-content:space-around;width:calc(50% - 75px)}.Navbar .Navbar__Container .Navbar__SubContainer .Navbar__Link{color:#fcda67;font-size:24px}.Navbar .Navbar__Container .Navbar__Logo{position:absolute;top:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);-webkit-transition:0.3s all ease-in;transition:0.3s all ease-in;-webkit-transform-origin:center top;transform-origin:center top}.Navbar .Navbar__Container .Navbar__Logo img{width:150px}@media screen and (max-width: 768px){.Navbar{height:75px}.Navbar.Navbar--collapsed{height:60px;background:rgba(100,191,200,0.9)}.Navbar.Navbar--collapsed .Navbar__MobileContainer .Navbar__Logo{-webkit-transform:translateX(-50%) scale(0.6);transform:translateX(-50%) scale(0.6)}.Navbar .Navbar__MobileContainer{padding:0 15px;display:block}.Navbar .Navbar__MobileContainer .Navbar__Logo{position:absolute;top:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);-webkit-transition:0.3s all ease-in;transition:0.3s all ease-in;-webkit-transform-origin:center top;transform-origin:center top}.Navbar .Navbar__MobileContainer .Navbar__Logo img{width:100px}}

.Banner{height:100vh}

.CustomCarousel{position:relative}.CustomCarousel>div{position:absolute;top:0;left:0;width:100%;height:100%;-webkit-transition:opacity 1s ease-in-out;transition:opacity 1s ease-in-out}.CustomCarousel>div.hide{opacity:0;visibility:hidden}.CustomCarousel>div img{width:100%;height:100%;object-fit:contain}

.Meetup{font-family:"Raleway", sans-serif}.Meetup .maxWidthContainer{padding:50px 40px;display:flex;justify-content:space-around;align-items:center}.Meetup .Meetup__Title{font-size:32px;letter-spacing:1px;font-weight:bold}.Meetup .Meetup__FindUs{margin-top:40px;font-size:24px;font-weight:500;display:flex;align-items:center}.Meetup .Meetup__FindUs .Meetup__MeetupIcon{width:125px;margin-left:15px}.Meetup .Meetup__GrpName{font-size:18px;font-weight:500;margin-top:5px}.Meetup .CustomCarousel{width:35%;padding:35% 0 0;margin-left:30px}

.Footer{background:#64bfc8;font-family:"Lato", sans-serif;color:#ffffff;text-align:center}.Footer>div{padding:15px 40px}

.DrawPrize .maxWidthContainer{padding:50px 40px}.DrawPrize .maxWidthContainer .DrawPrize-Title{font-family:"Raleway", sans-serif;font-size:32px;font-weight:700;color:#333333;padding-bottom:30px;margin-bottom:40px;border-bottom:1px solid #666666;text-align:center}.DrawPrize .maxWidthContainer .DrawPrize-Users{display:flex;flex-wrap:wrap;justify-content:center}.DrawPrize .maxWidthContainer .DrawPrize-Users .DrawPrize-User{position:relative;margin:10px}.DrawPrize .maxWidthContainer .DrawPrize-Users .DrawPrize-User>img{width:100px;height:100px;border-radius:50px;object-fit:contain}.DrawPrize .maxWidthContainer .DrawPrize-Users .DrawPrize-User .DrawPrize-Plus3{position:absolute;left:0;top:0;width:100px;height:100px;border-radius:50px;background-color:rgba(0,0,0,0.3);display:flex;justify-content:center;align-items:center;color:#ffffff;font-size:24px;font-family:"Lato", sans-serif}.DrawPrize .maxWidthContainer .DrawPrize-Users .DrawPrize-User .DrawPrizeBlink{position:absolute;left:0;top:0;width:100px;height:100px;border-radius:50px;background-color:rgba(255,255,255,0.9);opacity:0}.DrawPrize .maxWidthContainer .DrawPrize-Users .DrawPrize-User .DrawPrizeBlink>img{width:100%}.DrawPrize .maxWidthContainer .DrawPrize-Btn{cursor:pointer;margin:40px auto;width:350px;height:50px;border-radius:6px;background-color:#fcda67;color:#333333;text-align:center;font-family:"Raleway", sans-serif;font-size:24px;font-weight:600;line-height:30px;padding:10px 0}.winnerOverlay{position:fixed;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center;width:100%;height:100%;background-color:rgba(0,0,0,0.55);color:#3a3a3a;overflow-y:hidden;z-index:11;-webkit-transform:scale(0.1);transform:scale(0.1);-webkit-transition:0.3s all ease-in-out;transition:0.3s all ease-in-out}.winnerOverlay *{box-sizing:border-box}.winnerOverlay.ReactModal__Overlay--after-open{display:flex;opacity:1;-webkit-transform:scale(1);transform:scale(1)}.winnerOverlay.ReactModal__Overlay--before-close{display:flex;opacity:0;-webkit-transform:scale(0.1);transform:scale(0.1)}.winnerOverlay .winnerModal{text-align:center;width:80%;max-width:460px;padding:50px 60px;box-sizing:border-box;background-color:#e6e6e6;box-shadow:0 2px 16px 0 #000000;color:#000000;font-family:"Raleway", serif;font-size:20px;line-height:26px}.winnerOverlay .winnerModal:focus{outline:none}.winnerOverlay .winnerModal img{width:150px;height:150px;border-radius:50%;margin:10px 0;padding:3px;border:3px solid #333333}.winnerOverlay .winnerModal .headLine{font-family:"Raleway", serif;font-size:26px;font-weight:600;line-height:32px;margin-bottom:10px}.winnerOverlay .winnerModal .winnerName{font-family:"Lato", sans-serif;margin-bottom:10px}

.CategorySelector{height:100vh;font-family:"Raleway", sans-serif}.CategorySelector>div{display:flex;align-items:center;height:100%;padding-top:110px;box-sizing:border-box}.CategorySelector .CategorySelector__Content{text-align:center}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory{display:inline-block;width:300px;font-size:24px;line-height:30px;padding:50px 0;text-align:center;border-radius:12px;margin:20px;position:relative;text-transform:uppercase;color:#fff;cursor:pointer;-webkit-transition:0.3s all ease-in-out;transition:0.3s all ease-in-out}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory:hover{box-shadow:0 0 2px 2px rgba(0,0,0,0.2)}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory:hover .CategorySelector__ImageBg>img{-webkit-transform:scale(1.1);transform:scale(1.1)}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory .CategorySelector__ImageBg{position:absolute;top:0;left:0;width:100%;height:100%;z-index:-2;overflow:hidden;border-radius:12px}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory .CategorySelector__ImageBg>img{width:100%;height:100%;object-fit:cover;-webkit-transition:0.3s all ease-in-out;transition:0.3s all ease-in-out}.CategorySelector .CategorySelector__Content .CategorySelector__SingleCategory .CategorySelector__ImageOverlay{position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;background:rgba(33,33,33,0.5);border-radius:12px}

*{box-sizing:border-box}body,html{margin:0}a{text-decoration:none}.maxWidthContainer{width:100%;max-width:1280px;margin:0 auto;padding:0 40px}.isDesktopOnly{display:block}.isMobileOnly{display:none}@media screen and (max-width: 768px){.isDesktopOnly{display:none !important}.isMobileOnly{display:block !important}}

